.footer {
	padding: 25rem 0 50rem;
	background-image: url('../img/footer.png');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	height: 337rem;
	margin-top: -13rem;

	@media (max-width: 991px) {
		height: auto;
		padding: 25rem 15rem 0;
	}

	&__content {
		max-width: 1500rem;
		margin: auto;
		display: flex;
		align-items: center;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__logo {
		display: block;
		width: 354rem;
		height: 165rem;

		@media (max-width: 991px) {
			margin: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__socials {
		margin-top: 10rem;
		display: flex;

		@media (max-width: 991px) {
			margin-top: 20rem;
		}
	}

	&__social+&__social {
		margin-left: 40rem;
	}

	&__social {
		width: 110rem;
		height: 94rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&:hover {
			opacity: 0.8;
		}
	}

	&__list {
		display: flex;

		@media (max-width: 991px) {
			margin: 30rem 0;
		}
	}

	&__item+&__item {
		margin-left: 30rem;
	}

	&__link {
		display: block;
		height: 36rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}

		&:hover {
			opacity: 0.8;
		}
	}

	&__form {
		width: 385rem;
		height: 312rem;
		background-image: url('../img/subscribe.png');
		background-size: 100% 100%;
		background-position: center;
		background-repeat: no-repeat;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		padding-bottom: 15rem;
	}

	&__input {
		width: 253rem;
		margin: 0 auto;
		font-family: "BebasNeue",
			sans-serif;
		font-weight: 400;
		font-size: 36rem;
		line-height: 116%;
		border: 3px solid #755334;
		border-radius: 15rem;
		transform: rotate(1.2deg);
		color: #47321F;
		opacity: 0.5;
		transform: rotate(1.2deg);
		outline: none;
		padding: 15rem;
	}

	&__btn {
		margin: 20rem auto 0;
		width: 238rem;
		height: 72rem;
		transition: all 0.3s ease;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&:hover {
			opacity: 0.8;
		}
	}
}