.intro {
	background-image: url('../img/bg-intro.png');
	background-size: 100% 100%;
	background-position: center;
	background-repeat: no-repeat;
	padding: 150rem 0 100rem;

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__img {
		width: 485rem;
		height: 584rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__play {
		margin-top: 20rem;
		width: 466rem;
		height: 164rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&:hover {
			opacity: 0.8;
		}
	}

	&__links {
		margin-top: 30rem;
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__link+&__link {
		margin-left: 60rem;

		@media (max-width: 991px) {
			margin: 30rem 0 0 0;
		}
	}

	&__link {
		width: 356rem;
		height: 108rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&:hover {
			opacity: 0.8;
		}
	}
}