.content {
	background-image: url('../img/bg-content.png');
	background-size: 100% 100%;
	background-position: center;
	background-repeat: no-repeat;
	margin-top: -40rem;
	padding: 0 15rem;

	@media (max-width: 991px) {
		background-size: cover;
		height: auto;
	}
}

.tore {
	padding-top: 150rem;

	&__text {
		background-image: url('../img/bg-tore.png');
		background-size: 100% 100%;
		background-position: center;
		background-repeat: no-repeat;
		height: 618rem;
		max-width: 1302rem;
		margin: 15rem auto 0;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;

		@media (max-width: 991px) {
			padding-top: 40rem;
			margin-top: 50rem;
			height: 400rem;
		}

		p {
			max-width: 945rem;
			font-weight: 600;
			font-size: 36rem;
			line-height: 44rem;
			text-align: center;
			color: #FFE097;

			@media (max-width: 991px) {
				max-width: 465rem;
				font-size: 22rem;
				line-height: 34rem;
			}
		}

		p+p {
			margin-top: 20rem;
		}
	}
}

.features {
	padding-top: 50rem;

	&__items {
		max-width: 1495rem;
		margin: 50rem auto 0;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 32rem 46rem;

		@media (max-width: 991px) {
			grid-template-columns: repeat(1, 1fr);
		}
	}

	&__item {
		background-size: 100% 100%;
		background-position: center;
		background-repeat: no-repeat;
		display: flex;
		padding-top: 60rem;

		@media (max-width: 991px) {
			padding-top: 30rem;
		}

		&:nth-child(1) {
			justify-content: flex-end;
			background-image: url('../img/f1.png');
			width: 714rem;
			height: 284rem;
			padding-right: 60rem;

			@media (max-width: 991px) {
				width: 514rem;
				height: 214rem;
				padding-right: 0;
			}
		}

		&:nth-child(2) {
			background-image: url('../img/f2.png');
			width: 690rem;
			height: 284rem;
			padding-left: 60rem;

			@media (max-width: 991px) {
				width: 530rem;
				height: 234rem;
				padding-left: 30rem;
			}
		}

		&:nth-child(3) {
			justify-content: flex-end;
			background-image: url('../img/f3.png');
			width: 714rem;
			height: 296rem;
			padding-right: 60rem;

			@media (max-width: 991px) {
				width: 514rem;
				height: 214rem;
				padding-right: 0;
			}
		}

		&:nth-child(4) {
			background-image: url('../img/f4.png');
			width: 787rem;
			height: 318rem;
			padding-left: 60rem;

			@media (max-width: 991px) {
				width: 530rem;
				height: 228rem;
				padding-left: 30rem;
			}
		}
	}

	&__text {
		max-width: 420rem;

		@media (max-width: 991px) {
			max-width: 340rem;
			padding-right: 20rem;
		}
	}

	&__title {
		font-family: "BebasNeue",
			sans-serif;
		font-size: 32rem;
		line-height: 37rem;
		text-transform: capitalize;
		color: #FFE097;

		@media (max-width: 991px) {
			font-size: 26rem;
		}
	}

	&__desc {
		margin-top: 10rem;
		font-weight: 600;
		font-size: 20rem;
		line-height: 24rem;
		color: #FFE097;
		text-shadow: 0px 4px 20px rgba(0, 0, 0, 0.8);

		@media (max-width: 991px) {
			margin-top: 5rem;
			font-size: 16rem;
		}
	}
}

.nft {
	padding-top: 100rem;

	&__items {
		max-width: 1500rem;
		margin: 80rem auto 0;
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-gap: 100rem;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 50rem;
		}
	}

	&__item {
		background-image: url('../img/frame.png');
		background-size: 100% 100%;
		background-position: center;
		background-repeat: no-repeat;
		height: 406rem;
		display: flex;
		align-items: center;
		justify-content: center;

		@media (max-width: 991px) {
			height: 345rem;
		}
	}
}

.instructions {
	padding-top: 200rem;

	@media (max-width: 991px) {
		padding-top: 100rem;
	}

	&__content {
		margin: auto;
		max-width: 1229rem;
		height: 812rem;

		@media (max-width: 991px) {
			height: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.tokenomics {
	padding-top: 100rem;

	&__content {
		display: flex;
		justify-content: space-between;
		max-width: 1200rem;
		margin: 35rem auto 0;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: center;
		}
	}

	&__img {
		width: 100%;
		max-width: 382rem;
		height: 388rem;

		@media (max-width: 991px) {
			height: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__text {
		max-width: 710rem;
		height: 404rem;
		background-image: url('../img/bg-token.png');
		background-size: 100% 100%;
		background-position: center;
		background-repeat: no-repeat;
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 0 50rem;

		@media (max-width: 991px) {
			margin-top: 40rem;
		}
	}

	&__title {
		font-family: "BebasNeue",
			sans-serif;
		font-size: 48rem;
		line-height: 48rem;
		color: #755334;
	}

	&__desc {
		margin-top: 15rem;
		font-weight: 600;
		font-size: 32rem;
		line-height: 39rem;
		color: #755334;

		@media (max-width: 991px) {
			font-size: 24rem;
			line-height: 29rem;
		}
	}

}

.roadmap {
	padding: 200rem 0 100rem;

	@media (max-width: 991px) {
		padding-top: 100rem;
	}

	&__map {
		margin: auto;
		max-width: 1611rem;
		height: 732rem;

		@media (max-width: 991px) {
			max-width: 530rem;
			height: auto;
			margin-top: 20rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.partners {
	padding: 100rem 0 60rem;

	@media (max-width: 991px) {
		padding-top: 0;
	}

	&__title {
		@media (max-width: 991px) {
			width: 500rem;
		}
	}

	&__items {
		max-width: 870rem;
		margin: 40rem auto 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}

	&__item {
		padding: 20rem;
		width: 228rem;
		height: 212rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

	}
}

