@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal; }

@font-face {
  font-family: "BebasNeue";
  src: url("../fonts/../fonts/BebasNeueBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal; }

html {
  box-sizing: border-box;
  font-size: 0.052083333333333336vw;
  height: 100%; }
  @media (max-width: 991px) {
    html {
      font-size: 0.18vw; } }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-size: 16rem;
  min-width: 320rem;
  height: 100%;
  font-family: "Montserrat", sans-serif; }

body.lock {
  overflow: hidden; }

input {
  border: none; }
  input::placeholder {
    opacity: 1; }

textarea {
  border: none;
  resize: none; }
  textarea::placeholder {
    opacity: 1; }

img {
  max-width: 100%; }

a {
  text-decoration: none;
  color: #fff;
  transition: all 0.3s ease; }
  a:hover {
    color: #8818CD; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

button {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0; }

.wrapper {
  position: relative;
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column; }

.main {
  flex: 1 1 auto; }

.title {
  text-align: center; }
  @media (max-width: 991px) {
    .title {
      width: 320rem;
      height: auto;
      margin: auto; }
      .title img {
        width: 100%;
        height: 100%;
        object-fit: contain; } }

