.header {
	background-image: url('../img/header.png');
	background-size: 100% 100%;
	background-position: center;
	background-repeat: no-repeat;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 133rem;
	display: flex;
	justify-content: center;

	@media (max-width: 991px) {
		height: 100rem;
	}

	&__list {
		display: flex;
		padding: 24rem;

		@media (max-width: 991px) {
			padding: 19rem;
		}
	}

	&__item+&__item {
		margin-left: 120rem;

		@media (max-width: 991px) {
			margin-left: 30rem;
		}
	}

	&__link {
		&:hover {
			opacity: 0.8;
		}
	}
}